// todo 
Utils.getNickNames = function(users) {
  return users.map(user => {
    return user.account === store.state.userUID? '' : user.nick
  })
}

// todo 
Utils.getTeamUpdateInfo = function(msg) {
  let text, team = msg.attach.team, op = this.getNickNames(msg.attach.users).pop()
  if (team['name']) {
    text = `${op}${team['name']}`
  } else if (team['intro']) {
    text = `${op}${team['intro']}`
  } 
  //  Android iOS 
  // else if (team['announcement']) {
  //   text = `${op}${team['announcement']}`
  // } 
  else if (team['joinMode']) {
    text = `${team.joinMode === 'noVerify' ? '' : team.joinMode === 'needVerify' ? '' : ''}`
  } else if (team['inviteMode']) {
    text = `${team['inviteMode'] === 'all' ? '' : ''}`
  } else if (team['updateTeamMode']) {
    text = `${team['updateTeamMode'] === 'all' ? '' : ''}`
  } else if (team['beInviteMode']) {
    text = `${team['beInviteMode'] === 'noVerify' ? '' : ''}`
  } else {
    text = ''
  }
  return text
}

